import react,{useState, useEffect, useRef} from 'react'
import logo from 'assets/images/logo.png'
import AdminApi from '../../../Api/AdminApi'

const Login = () => {

	const idRef = useRef()
	const pwdRef = useRef()

	const postLogin = () => {

		AdminApi.postLogin(idRef.current.value, pwdRef.current.value)
			.then(res => {
				console.log(res.data.data.token)
				localStorage.setItem('auth',res.data.data.token)
			})


	}

	return (
		<div id="wrap">
			<main className="login">
				<div className="login_box">
					<div>
						<h2 className="login_logo">
							<img src={logo} alt="IntelliVIX logo" />
						</h2>
						<p className="fs_43 f_ebold txt_center col_black martop_33"></p>
						<p className="fs_19 col_grey2 txt_center martop_10">   .</p>

						<div className="form martop_30">
							<p>
								<i className="user_id_icon" />
								<input id="userId" type="text" className="user_id w_full" placeholder=" ." ref={idRef} />
							</p>
							<p className="martop_15">
								<i className="user_pw_icon" />
								<input id="userPw" type="password" className="user_pw w_full" placeholder=" ." ref={pwdRef} />
							</p>

							<input type="checkbox" className="ch_check hide" id="inpChk1" />
							<label htmlFor="inpChk1" className="label marleft_10 martop_20"> </label>

							<p className="martop_17">
								<button className="blue_btn login w_full f_ebold alert_btn" onClick={()=>postLogin()}></button>
								{/*}	<!--  . -->
								<!-- <button class="blue_btn login w_full f_ebold alert_btn disabled"></button> --> */}
							</p>
						</div>
						<p className="martop_17 col_grey2 fs_16">     .</p>
					</div>
					<div className="login_footer">
						<p>Copyright 2021 Intellivix Co., Ltd. All rights reserved.</p>
					</div>
				</div>


				<div className="modal_wrap alert">
					<div className="modal_section">
						<button className="hide_btn delete_modal" />
						<p className="modal_text" />
						<div className="btn_wrap">
							<button type="button" className="blue_btn w_100 delete_modal"></button>
						</div>
					</div>
				</div>
			</main>
		</div>
	)
}

export default Login
